</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      activeIndex: '1',
      msg: 'Welcome to Your Vue.js App'
    }
  },
  methods: {
    handleSelect (key, keyPath) {
      console.log(key, keyPath)
    },
    toLogin: function () {
      // this.$router.push({path: '/login'})
      this.$http.get('/login')
    }
  }
}
</script>

